/*--------------------------------------------------------------
  2.0 Edtior Styles
--------------------------------------------------------------*/
.editor-styles-wrapper h1 {
	font-size: var(--wp--preset--font-size--gigantic) !important;
}

button.ksd-editor-btn.ksd-editor-btn--placeholder {
	margin: 1rem;
	padding: 0.5rem 1rem;
	background-color: transparent;
	color: grey;
	border: 2px solid grey;
}

.components-popover.block-editor-block-popover.block-editor-block-list__block-popover {
	z-index: 9999;
}

/* Image preview buttons */
.image-block-buttons,
.components-panel__row {
	position: relative;
	z-index: 9998;
}

.image-block-buttons {
	display: flex;
	flex-direction: column;
}

.image-block-buttons__update {
	display: flex;
}

.image-block-buttons__update {
	white-space: nowrap;
}

/**
 * Aligment Controls
 */
.editor-alignment-controls > div > p {
	margin-bottom: 0;
}

.editor-alignment-controls > div > .components-button-group {
	display: flex;
	gap: 0.175em;
	margin-top: 0.25em;
	margin-bottom: 1em;
}

.editor-alignment-controls
	> div
	> .components-button-group
	> button.is-pressed
	svg {
	fill: "white";
}

.editor-max-width-controls {
	position: relative;
	display: flex;
	flex-direction: column;
}

.editor-max-width-controls > .components-range-control {
	margin-bottom: 0;
}

.editor-max-width-controls
	> .components-range-control
	> .components-base-control__field {
	margin-bottom: 0;
}
.editor-max-width-controls .components-button {
	position: absolute;
	top: 0;
	right: 0;
}

/**
 * Gap Controls
 */
.editor-gap-controls .components-range-control__root {
	flex-direction: column;
}

.editor-gap-controls .components-base-control__label {
	margin-bottom: 1.5em;
}

.editor-gap-controls .components-range-control__wrapper {
	max-width: calc(100% - 2em);
	margin-inline: auto;
}

.editor-gap-controls .components-range-control__root > span {
	margin: 0;
}

.editor-gap-controls .components-range-control__mark-label {
	top: 1rem;
}
/*--------------------------------------------------------------
  3.0 Modifiers
--------------------------------------------------------------*/
/**
 * SPACING
 */
section {
	width: var(--wp--custom--content-size);
	margin-inline: auto;
}

/* Set default margin and padding excluding hero */
section:not(section:first-of-type) {
	margin-top: var(--wp--preset--spacing--60);
}

/* Set default margin and padding excluding last element */
section:not(section:last-of-type) {
	margin-bottom: var(--wp--preset--spacing--60);
}

/**
 * ALIGNMENT
 */
.has-text-align-left {
	text-align: left;
}

.has-text-align-center {
	text-align: center;
}

.has-text-align-right {
	text-align: right;
}

.has-text-align-justify {
	text-align: justify;
}

/**
* GRID
*/
:where(.is-stacked) {
	display: grid;
	grid-template-areas: "stack";
}

:where(.is-stacked > *) {
	grid-area: stack;
}

/** 
* GRID COLUMNS 
*/
:where(
		.grid-cols-1,
		.grid-cols-2,
		.grid-cols-3,
		.grid-cols-4,
		.grid-cols-5,
		.grid-cols-6
	) {
	display: grid;
	grid-template-columns: repeat(1, minmax(0, 1fr));
	margin-inline: auto;
	gap: var(--wp--preset--spacing--40);
}

:is(.grid-cols-2, .grid-cols-3, .grid-cols-4, .grid-cols-5, .grid-cols-6) h1,
:is(.grid-cols-2, .grid-cols-3, .grid-cols-4, .grid-cols-5, .grid-cols-6) h2,
:is(.grid-cols-2, .grid-cols-3, .grid-cols-4, .grid-cols-5, .grid-cols-6)
	> div
	> p:first-child {
	margin-top: 0;
}

@media screen and (min-width: 800px) {
	:where(.grid-cols-1, .grid-cols-1-rv) {
		grid-template-columns: repeat(1, minmax(0, 1fr));
	}

	:where(.grid-cols-2, .grid-cols-2-rv) {
		grid-template-columns: repeat(2, minmax(0, 1fr));
	}

	:where(.grid-cols-70-30, .grid-cols-70-30-rv) {
		display: grid;
		grid-template-columns: 0.67fr 0.33fr;
	}

	:where(.grid-cols-30-70, .grid-cols-30-70-rv) {
		display: grid;
		grid-template-columns: 0.33fr 0.67fr;
	}

	:where(.grid-cols-75-25, .grid-cols-75-25-rv) {
		display: grid;
		grid-template-columns: 0.75fr 0.25fr;
	}

	:where(.grid-cols-25-75, .grid-cols-25-75-rv) {
		display: grid;
		grid-template-columns: 0.75fr 0.25fr;
	}

	:where(.grid-cols-3) {
		grid-template-columns: repeat(3, minmax(0, 1fr));
	}

	:where(.grid-cols-4) {
		grid-template-columns: repeat(4, minmax(0, 1fr));
	}

	:where(.grid-cols-5) {
		grid-template-columns: repeat(5, minmax(0, 1fr));
	}

	:where(.grid-cols-6) {
		grid-template-columns: repeat(6, minmax(0, 1fr));
	}

	:where(
			:is(
					.grid-cols-2-rv,
					.grid-cols-70-30-rv,
					.grid-cols-75-25-rv,
					.grid-cols-30-70-rv,
					.grid-cols-25-75-rv,
					.is-reversed
				)
				div:first-child
		) {
		order: 2;
	}

	:where(
			:is(
					.grid-cols-2-rv,
					.grid-cols-70-30-rv,
					.grid-cols-75-25-rv,
					.grid-cols-30-70-rv,
					.grid-cols-25-75-rv,
					.is-reversed
				)
				div:last-child
		) {
		order: 1;
	}
}

/**
 * BUTTONS
 */
/* Default styles */
.wp-block-button__link {
	--_background-color: var(--wp--preset--color--primary);
	--_text-color: var(--wp--preset--color--white);

	--_border-color: var(--wp--preset--color--grey-light);
	--_border: 4px solid var(--_border-color);
	--_radius: 0;

	--_size: 15rem;

	--_shadow-color: var(--wp--preset--color--grey-light);
	--_shadow-base: 0.05em;
	--_shadow: var(--_shadow-base) var(--_shadow-base) 0px 0px
			var(--_shadow-color),
		calc(var(--_shadow-base) * 2) calc(var(--_shadow-base) * 2) 0px 0px
			var(--_shadow-color),
		calc(var(--_shadow-base) * 3) calc(var(--_shadow-base) * 3) 0px 0px
			var(--_shadow-color),
		calc(var(--_shadow-base) * 4) calc(var(--_shadow-base) * 4) 0px 0px
			var(--_shadow-color),
		calc(var(--_shadow-base) * 5) calc(var(--_shadow-base) * 5) 0px 0px
			var(--_shadow-color);

	position: relative;

	display: flex;
	align-items: center;
	justify-content: center;

	font-size: calc(var(--wp--preset--font-size--medium) * 1.2);
	font-family: var(--wp--preset--font-family--accent);
	color: var(--_text-color);
	font-weight: bold;
	text-transform: uppercase;
	line-height: 1.2;
	white-space: nowrap;

	padding-block: 1em;
	padding-inline: 3em;

	min-width: var(--_size) !important;

	background: var(--_background-color);

	border-radius: var(--_radius);
	border: var(--_border);

	box-shadow: var(--_shadow);
	user-select: none;
	-webkit-user-select: none;
	touch-action: manipulation;
}

/* Set disabled state */
:is(a, button).wp-block-button__link:disabled {
	color: grey;
	pointer-events: none;
	visibility: hidden;
}

/* Fill Styles */
.is-style-primary-fill > .wp-block-button__link {
	--_border-color: var(--wp--preset--color--primary);
	--_background-color: var(--wp--preset--color--primary);
	--_text-color: var(--wp--preset--color--white);
}

.is-style-secondary-fill > .wp-block-button__link {
	--_border-color: var(--wp--preset--color--foreground);
	--_background-color: var(--wp--preset--color--foreground);
	--_text-color: var(--wp--preset--color--background);
}

/* Outline Styles */
:is(.is-style-primary-outline, .is-style-secondary-outline)
	> .wp-block-button__link {
	background: none !important;
}

.is-style-primary-outline > .wp-block-button__link {
	--_background-color: var(--wp--preset--color--primary);
	--_text-color: var(--wp--preset--color--grey-light);
}

.is-style-secondary-outline > .wp-block-button__link {
	--_border-color: var(--wp--preset--color--foreground);
	--_background-color: var(--wp--preset--color--foreground);
	--_text-color: var(--wp--preset--color--foreground);
}

/**
 * LIST
 */
ul.is-style-custom-icon li {
	display: flex;
}

/**
 * IS VISUALLY HIDDEN
 */
.is-visually-hidden {
	position: relative;
	width: fit-content;
}

.is-visually-hidden::after {
	--_color: white;
	--_background: rgba(255, 0, 0, 0.75);
	content: "Hidden visually";
	position: absolute;
	right: 0;
	bottom: 0;

	display: grid;
	place-items: center;

	font-size: 0.5rem;
	background-color: var(--_background);
	color: var(--_color);

	padding: 1em 2em;

	z-index: 9999;
}
